import React from "react";
import "./styles.css";

export default function App() {
  const client_id = process.env.REACT_APP_CLIENT_ID;
  const api_key = process.env.REACT_APP_API_KEY;

  return (
    <div className="App">
      <p>Client ID: {client_id}</p>
      <p>API KEY: {api_key}</p>
    </div>
  );
}
